var db = {
    dbkey:'todolist-vue2',

    /**
     * [fetch ]
     * @type {[type]}
     */
    fetch(){
        return JSON.parse(localStorage.getItem(this.dbkey) || '[]');
    },

    /**
     * [save todo Obect]
     * @param  {[Obejct]} todo [todo]
     * @return {[type]}        [description]
     */
    save(todoList){
        localStorage.setItem(this.dbkey,JSON.stringify(todoList));
    }
};

export default db;


