require('../../css/source/index.css');

import Vue from 'vue';
import DB from './DB';

let todoList = new Vue({
    el: '#todoContainer',
    data: {
        // modeltodo
        todo: '',

        // palceholder
        placeholder: '',

        // todo
        todolist: DB.fetch(),

        // todo
        STATUS: {
            ALL: 'all',
            ACTIVE: 'active',
            COMPLETED: 'completed'
        },

        alldone: false,

        // 
        filterStaus: 'all',

        editedTodo: null
    },
    methods: {
        /**
         * [addNewTodo todo]
         */
        addNewTodo() {
            let content = String(this.todo);
            if (content.length === 0) {
                return;
            }

            let doto = {
                content: content,
                status: this.STATUS.ACTIVE,
                // input
                isCompleted: false
            };

            this.todolist.unshift(doto);
            this.todo = '';
        },

        /**
         * [doneTodo todo]
         * @param  {[type]} index [description]
         * @return {[type]}       [description]
         */
        doneTodo: function(index) {
            // this.todolist[index].isCompleted = !this.todolist[index].isCompleted;
            this.todolist[index].status = this.todolist[index].isCompleted ? this.STATUS.COMPLETED : this.STATUS.ACTIVE;
        },

        /**
         * [deleteTodo ]
         * @param  {[type]} index [description]
         * @return {[type]}       [description]
         */
        deleteTodo(index) {
            this.todolist.splice(index, 1);
        },

        /**
         * [delteCompleted ]
         * @return {[type]} [description]
         */
        delteCompleted() {
            this.todolist = this.todolist.filter(item => {
                return item.status != this.STATUS.COMPLETED;
            });
        },

        /**
         * [editTodo ]
         * @return {[type]} [todo]
         */
        editTodo(todo) {
            this.editedTodo = todo;
            this.backup = todo.content;
        },

        /**
         * [doneEdit ]
         * @param  {[type]} todo [description]
         * @return {[type]}      [description]
         */
        doneEdit(todo, index) {

            if (!todo.content) {
                this.deleteTodo(index);
                return;
            }

            if (!this.editedTodo) {
                return;
            }
            todo.content = todo.content.trim();
            this.editedTodo = null;
            this.backup = null;
        },

        /**
         * [cancelEdit ]
         * @param  {[type]} todo [description]
         * @return {[type]}      [description]
         */
        cancelEdit(todo) {
            this.editedTodo = null;
            this.backup = null;
            todo.content = this.backup;
        }
    },
    computed: {
        /**
         * [filterList ]
         */
        filterList() {

            if (this.filterStaus === this.STATUS.ALL) {
                return this.todolist;
            }

            return this.todolist.filter(item => {
                return item.status === this.filterStaus;
            });

        },

        /**
         * [remains ]
         * @return {[type]} [description]
         */
        remains() {
            return this.todolist.length;
        },

        alldoneTitle() {
            return this.alldone ? '' : '';
        }
    },
    watch: {
        /**
         * [todolist todolistwatch,]
         * @type {Object}
         */
        todolist: {
            deep: true,
            handler: function() {
                DB.save(this.todolist);
            }
        },

        /**
         * [alldone ]
         * @type {Object}
         */
        alldone: {
            handler: function(val, oldval) {

                let status = val ? this.STATUS.COMPLETED : this.STATUS.ACTIVE;
                let isCompleted = val;
                this.todolist.forEach(item => {
                    item.status = status;
                    item.isCompleted = isCompleted;
                });
            }
        }
    },
    directives:{
        focus:{
            update: function(el, binding, vnode){
                if (binding.value) {
                    el.focus();
                }
            }
        }
    }
});


